/* Masonry Layout Overrides */
/*
  Activate masonry mode by adding .is-masonry to the .row container.
  In masonry mode, the layout switches to a CSS Columns model.
  Desktop column count is set with helper classes (.col-#),
  with optional overrides for tablet (.col-#-tablet) and phone (.col-#-phone).
*/
.row.is-masonry, .is-masonry {
  display: block;
  column-gap: 1.5rem;
  /* Override children styles for CSS Columns */
  /* Desktop helper: .col-# sets column-count */
}
.row.is-masonry > [class*=grid-], .is-masonry > [class*=grid-] {
  float: none !important;
  width: auto !important;
  display: inline-block;
  break-inside: avoid;
  margin-bottom: 1.5rem;
}
.row.is-masonry > .full-width, .is-masonry > .full-width {
  column-span: all;
  -webkit-column-span: all;
  display: block;
  width: 100%;
  break-inside: avoid;
  margin-bottom: 1.5rem;
}
.row.is-masonry.col-1, .is-masonry.col-1 {
  column-count: 1;
}
.row.is-masonry.col-2, .is-masonry.col-2 {
  column-count: 2;
}
.row.is-masonry.col-3, .is-masonry.col-3 {
  column-count: 3;
}
.row.is-masonry.col-4, .is-masonry.col-4 {
  column-count: 4;
}
.row.is-masonry.col-5, .is-masonry.col-5 {
  column-count: 5;
}
.row.is-masonry.col-6, .is-masonry.col-6 {
  column-count: 6;
}
.row.is-masonry.col-7, .is-masonry.col-7 {
  column-count: 7;
}
.row.is-masonry.col-8, .is-masonry.col-8 {
  column-count: 8;
}
.row.is-masonry.col-9, .is-masonry.col-9 {
  column-count: 9;
}
.row.is-masonry.col-10, .is-masonry.col-10 {
  column-count: 10;
}
.row.is-masonry.col-11, .is-masonry.col-11 {
  column-count: 11;
}
.row.is-masonry.col-12, .is-masonry.col-12 {
  column-count: 12;
}

/* Tablet overrides: if .col-#-tablet is present, use that;
   otherwise default to 2 columns */
@media only screen and (min-width: 769px) and (max-width: 1024px) {
  .row.is-masonry.col-1-tablet, .is-masonry.col-1-tablet {
    column-count: 1 !important;
  }
  .row.is-masonry.col-2-tablet, .is-masonry.col-2-tablet {
