  }
  .row.is-masonry.col-12-tablet, .is-masonry.col-12-tablet {
    column-count: 12 !important;
  }
  .row.is-masonry:not([class*=-tablet]), .is-masonry:not([class*=-tablet]) {
    column-count: 2;
  }
}
/* Phone overrides: if .col-#-phone is present, use that;
   otherwise default to 1 column */
@media only screen and (max-width: 768px) {
  .row.is-masonry.col-1-phone, .is-masonry.col-1-phone {
