.bg-white {
  background-color: #ffffff !important;
  color: #1a1a1a;
}

.autosize {
  resize: vertical;
}

.bold-text {
  font-weight: bold;
}

.light-text {
  font-weight: lighter !important;
}

.italic {
  font-style: italic;
}

.color-in {
  color: inherit !important;
}

.block {
  display: block !important;
  margin-bottom: 8px;
}

.inline {
  display: inline !important;
}

.in-block {
  display: inline-block !important;
}

.ucase {
  text-transform: capitalize;
}

.cap {
  text-transform: uppercase;
}

.drop-shadow {
  -webkit-box-shadow: 0 1.5px 1.1px -1px #000;
  -moz-box-shadow: 0 1.5px 1.1px -1px #000;
  box-shadow: 0 1.5px 1.1px -1px #000;
}

.push-left {
  float: left !important;
}

.push-right {
  float: right !important;
}

.push-none {
  float: none !important;
}

.align-r, .align-right {
  text-align: right !important;
}

.align-c, .align-center, .align-centre {
  text-align: center !important;
}

.align-l, .align-left {
  text-align: left !important;
}

.naira::before, .fab .fa-naira::before {
  content: "₦";
}

textarea.autosize {
  resize: none;
  -webkit-transition: height 0.2s ease-in;
  -moz-transition: height 0.2s ease-in;
  -ms-transition: height 0.2s ease-in;
  -o-transition: height 0.2s ease-in;
  transition: height 0.2s ease-in;
  min-height: 120px;
}

.auto-width {
  width: auto !important;
}

.auto-height {
  height: auto !important;
}

.no-shadow {
  -webkit-box-shadow: none !important;
  -moz-box-shadow: none !important;
  box-shadow: none !important;
}

.no-radius {
  -webkit-border-radius: 0;
  -ms-border-radius: 0;
  -moz-border-radius: 0;
  border-radius: 0;
}

.view-space {
  position: relative;
  margin: auto;
  max-width: 1366px;
}
.view-space.vw-larger {
  max-width: 1920px;
}
.view-space.vw-medi {
  max-width: 1280px;
}
.view-space.vw-mini {
  max-width: 1024px;
}

table {
  width: 100%;
}
table tr:nth-of-type(odd) {
  background: rgba(0, 0, 0, 0.1);
}
table tr:hover {
  background: rgba(0, 0, 0, 0.2);
}
table.vertical thead, table.vertical tbody {
  position: relative;
}
table.vertical tr, table.vertical td, table.vertical th {
  padding: 10px;
}
table.vertical thead tr {
  background: inherit;
  padding: 10px;
}
table.vertical thead tr th {
  background: transparent;
}
table.vertical tbody tr {
  -webkit-transition: background 0.2s ease-in;
  -moz-transition: background 0.2s ease-in;
  -ms-transition: background 0.2s ease-in;
  -o-transition: background 0.2s ease-in;
  transition: background 0.2s ease-in;
}
table.vertical td:first-child, table.vertical th:first-child {
  border: none;
}
table.horizontal tr {
  width: 100% !important;
  margin: 0;
  padding: 0;
  border-bottom: solid 1px #afb6bb;
  -webkit-transition: all 0.2s ease-in;
  -moz-transition: all 0.2s ease-in;
  -ms-transition: all 0.2s ease-in;
  -o-transition: all 0.2s ease-in;
  transition: all 0.2s ease-in;
}
table.horizontal tr:last-child {
  border: none;
}
table.horizontal tr th {
  font-weight: bold;
  text-align: left;
}
table.horizontal tr th, table.horizontal tr td {
  padding: 8px;
}

.font-size-1-2 {
  font-size: 1.2em !important;
